import sock from './socket-object'

function send () {
    sock.send(document.getElementById('message').value)
    console.log('sending')
}


function Prompt () {

    
    return (
        <div className='container d-flex row gx-4'>
            <div className='col row'>
                <input className='input-prompt border-curve-10' id='message' type='text'></input>
            </div>
            <div className='col-1'>
                <button className='btn btn-primary border-curve-10' onClick={send}>^</button>
            </div>
        </div>
    )
}

export default Prompt